@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

body {
  display: grid;
  min-width: 320px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  grid-template-rows: auto 1fr auto;
}
@media (max-width: 768px) {
  body {
    font-size: 14px;
    line-height: 17px;
  }
}

img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.no-scroll {
  position: fixed;
  height: 100vh;
  height: calc(100 * var(--vh, 1vh));
  overflow: hidden;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scroll::-webkit-scrollbar {
  display: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.button {
  display: block;
  width: 100%;
  padding: 10px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  font-family: "Montserrat", "Arial", sans-serif;
  text-align: center;
  color: #011c40;
  text-decoration: none;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
}
.button:hover, .button:focus {
  color: #ffffff;
  background-color: #0059cd;
  outline: none;
}
.button:active {
  opacity: 0.5;
}
.button:disabled, .button--disabled {
  color: #b1b1b1;
  background-color: #c6c6c6;
}

.main h2 {
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
@media (max-width: 1023px) {
  .main h2 {
    margin: 0;
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767px) {
  .main h2 {
    font-size: 24px;
    line-height: 34px;
  }
}

.header {
  position: relative;
  min-height: 650px;
  color: #f9fbfd;
  background-color: #011c40;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background-image: url("../img/header-bg-desktop.jpg");
}
@supports (background-image: url("../img/header-bg-desktop.webp")) {
  .header {
    background-image: url("../img/header-bg-desktop.webp");
  }
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/header-bg-desktop@2x.jpg");
  }
  @supports (background-image: url("../img/header-bg-desktop@2x.webp")) {
    .header {
      background-image: url("../img/header-bg-desktop@2x.webp");
    }
  }
}
@media (max-width: 1023px) {
  .header {
    min-height: 638px;
    background-image: url("../img/header-bg-tablet.jpg");
  }
  @supports (background-image: url("../img/header-bg-tablet.webp")) {
    .header {
      background-image: url("../img/header-bg-tablet.webp");
    }
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/header-bg-tablet@2x.jpg");
  }
  @supports (background-image: url("../img/header-bg-tablet@2x.webp")) {
    .header {
      background-image: url("../img/header-bg-tablet@2x.webp");
    }
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 500px;
  }
}

.header__promo {
  padding: 160px 95px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .header__promo {
    padding: 184px 0;
  }
}
@media (max-width: 767px) {
  .header__promo {
    padding: 122px 0;
  }
}

.header__promo h1 {
  margin-bottom: 10px;
  font-size: 54px;
  line-height: 66px;
}
@media (max-width: 1023px) {
  .header__promo h1 {
    margin-bottom: 13px;
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .header__promo h1 {
    font-size: 36px;
    line-height: 45px;
  }
}

.header__promo p {
  margin: 0;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .header__promo p {
    font-size: 18px;
    line-height: 21px;
  }
}
@media (max-width: 767px) {
  .header__promo p {
    max-width: 230px;
    font-size: 16px;
    line-height: 22px;
  }
}

@media (max-width: 767px) {
  .header--open .header__promo {
    padding-left: 30px;
  }
}

.nav__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 36px;
}
@media (max-width: 1023px) {
  .nav__wrapper {
    padding-top: 30px;
  }
}

@media (max-width: 767px) {
  .nav__logo {
    position: absolute;
    top: 21px;
    left: 16px;
    z-index: 10;
  }
}

.nav__toggle {
  position: absolute;
  top: 13px;
  right: 10px;
  z-index: 10;
  display: none;
  width: 35px;
  height: 35px;
  padding: 0;
  background-color: transparent;
  background-image: url("../img/svg/icon-menu.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
}
@media (max-width: 767px) {
  .nav__toggle {
    display: block;
  }
}

@media (max-width: 767px) {
  .header--open .nav {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100vh;
    padding: 0;
    background-color: rgba(0, 43, 102, 0.5);
  }
}

@media (max-width: 767px) {
  .header--open .nav__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 37px;
    height: 100vh;
    padding-top: 85px;
    padding-bottom: 50px;
    overflow: auto;
    background-color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .header .nav__list {
    display: none;
  }
}

@media (max-width: 767px) {
  .header--open .nav__list {
    display: block;
  }
}

@media (max-width: 767px) {
  .header--open .nav__toggle {
    right: 8px;
    background-image: url("../img/svg/icon-close.svg");
  }
}

@media (max-width: 767px) {
  .header--open .nav__logo {
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .header--open .nav-menu__link {
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .header--open .nav-menu__item {
    margin-bottom: 28px;
  }
}

@media (max-width: 767px) {
  .no-js .nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .no-js .nav__wrapper {
    margin: 0 -15px;
    padding-top: 70px;
    padding-bottom: 40px;
    background-color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .no-js .nav__list {
    display: block;
  }
}

@media (max-width: 767px) {
  .no-js .nav__logo {
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .no-js .nav-menu__link {
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .no-js .nav-menu__item {
    margin-bottom: 28px;
  }
}

.nav-menu {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .nav-menu {
    flex-direction: column;
  }
}

.nav-menu__item {
  margin-left: 34px;
}
@media (max-width: 1023px) {
  .nav-menu__item {
    margin-left: 24px;
  }
}

.nav-menu__link {
  font-weight: 500;
  color: #f9fbfd;
  text-decoration: none;
}
@media (max-width: 1023px) {
  .nav-menu__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .nav-menu__link {
    font-size: 18px;
    line-height: 22px;
  }
}
.nav-menu__link:hover, .nav-menu__link:focus {
  text-decoration: 2px;
  outline: none;
  text-decoration-color: inherit;
  text-decoration-line: underline;
  text-decoration-style: solid;
  text-underline-offset: 6px;
}
.nav-menu__link:active {
  opacity: 0.5;
}

.logo {
  display: block;
  min-width: 70px;
  height: 38px;
  color: #ffffff;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .logo {
    min-width: 38px;
    height: 20px;
  }
}
.logo:not([href]) {
  pointer-events: none;
}
.logo[href]:hover, .logo[href]:focus {
  opacity: 0.7;
}
.logo[href]:active {
  opacity: 0.4;
}

.logo svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: currentColor;
}

.world {
  padding-top: 120px;
  padding-bottom: 145px;
}
@media (max-width: 1023px) {
  .world {
    padding-top: 92px;
    padding-bottom: 76px;
  }
}
@media (max-width: 767px) {
  .world {
    padding-top: 57px;
    padding-bottom: 85px;
  }
}

.world__container {
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 80px;
}
@media (max-width: 1023px) {
  .world__container {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .world__container {
    display: block;
  }
}

.world__content {
  padding-top: 34px;
}
@media (max-width: 1023px) {
  .world__content {
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .world__content {
    margin-bottom: 42px;
  }
}

.world__content p {
  line-height: 24px;
}
@media (max-width: 1023px) {
  .world__content p {
    font-size: 16px;
  }
}

.world__img {
  min-width: 290px;
  max-width: 560px;
}
@media (max-width: 1023px) {
  .world__img {
    max-width: 324px;
    margin-top: 8px;
    margin-right: 5px;
  }
}
@media (max-width: 767px) {
  .world__img {
    max-width: 100%;
    margin-top: 0;
    margin-right: 0;
  }
}

.world__img img {
  display: block;
  width: 100%;
  height: auto;
}

.advantage {
  padding-top: 87px;
  padding-bottom: 120px;
  background-color: #b9ddfb;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../img/advantage-bg.jpg");
}
@supports (background-image: url("../img/advantage-bg.webp")) {
  .advantage {
    background-image: url("../img/advantage-bg.webp");
  }
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .advantage {
    background-image: url("../img/advantage-bg@2x.jpg");
  }
  @supports (background-image: url("../img/advantage-bg@2x.webp")) {
    .advantage {
      background-image: url("../img/advantage-bg@2x.webp");
    }
  }
}
@media (max-width: 1023px) {
  .advantage {
    padding-top: 91px;
    padding-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .advantage {
    padding-top: 58px;
    padding-bottom: 64px;
  }
}

.advantage__container h2 {
  margin-bottom: 49px;
  text-align: center;
}
@media (max-width: 1023px) {
  .advantage__container h2 {
    margin-bottom: 51px;
  }
}
@media (max-width: 767px) {
  .advantage__container h2 {
    margin-bottom: 31px;
  }
}

.advantage__list {
