  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}
@media (max-width: 1023px) {
  .advantage__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantage__list {
    grid-template-columns: 1fr;
  }
}

.advantage__item {
  width: 100%;
  padding: 63px 30px 45px;
  background-color: #f9fbfd;
  background-repeat: no-repeat;
  background-position: 30px 30px;
}
@media (max-width: 1023px) {
  .advantage__item {
    padding: 53px 17px 20px 20px;
  }
}
@media (max-width: 767px) {
  .advantage__item {
    padding-right: 22px;
  }
}

.advantage__item--liner {
  background-image: url("../img/svg/icon-liner.svg");
  background-size: 27px 32px;
}
@media (max-width: 1023px) {
  .advantage__item--liner {
    background-position: 20px 25px;
    background-size: 21px 26px;
  }
}

.advantage__item--inclusive {
  background-image: url("../img/svg/icon-inclusive.svg");
  background-size: 38px 32px;
}
@media (max-width: 1023px) {
  .advantage__item--inclusive {
    background-position: 19px 25px;
    background-size: 27px 26px;
  }
}

.advantage__item--zodiac {
  background-image: url("../img/svg/icon-zodiac.svg");
  background-size: 44px 32px;
}
@media (max-width: 1023px) {
  .advantage__item--zodiac {
    background-position: 20px 27px;
    background-size: 36px 26px;
  }
}

.advantage__item h3 {
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 1023px) {
  .advantage__item h3 {
    margin-bottom: 9px;
    line-height: 25px;
  }
}
.advantage__item p {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
}

.cruises {
  padding: 62px 0 120px;
}
@media (max-width: 1023px) {
  .cruises {
    padding: 91px 0 100px;
  }
}
@media (max-width: 767px) {
  .cruises {
    padding: 58px 0 65px;
  }
}

.cruises__container {
  padding: 0 35px;
}
@media (max-width: 1023px) {
  .cruises__container {
    padding: 0 60px;
  }
}
@media (max-width: 767px) {
  .cruises__container {
